import React from 'react';
import { Box, Link, PseudoBox } from '@chakra-ui/core';

const Navbar = () => (
  <PseudoBox
    p="20px"
    borderBottom="1px solid lightgray"
    boxShadow="0px 5px 15px 0px rgba(0,0,0,0.2)"
    shadow="md"
  >
    <Link>Rick and Morty Wiki</Link>
  </PseudoBox>
);

export default Navbar;
